import React, { useEffect, useState } from 'react';
import { Loader } from '@googlemaps/js-api-loader';
import Section from '../Section';

interface CompanyProps extends React.HTMLProps<HTMLDivElement> {
  data: any;
}

const Company: React.FC<CompanyProps> = props => {
  useEffect(() => {
    const loader = new Loader({
      apiKey: 'AIzaSyBI_6Bn1nUDu3o677tqEmmW1KV7UhnKkCU',
      version: 'weekly'
    });

    loader.load().then(() => {
      const { google } = window as any;

      if (
        !document.getElementById('map') ||
        !google ||
        !google.maps ||
        !google.maps.Map
      ) {
        return;
      }

      const lat: number = 35.67612894074747;
      const lng: number = 139.71010032475465;

      const map: any = new (window as any).google.maps.Map(
        document.getElementById('map') as HTMLElement,
        {
          mapId: '13c8241e9ab7d73d',
          center: {
            lat,
            lng
          },
          zoom: 18
        }
      );

      const image: string = 'https://daruma-inv.com/pin.svg?gegea';

      const marker: any = new google.maps.Marker({
        position: { lat, lng },
        map,
        icon: {
          url: image,
          scaledSize: new google.maps.Size(60, 60)
        }
      });
    });
  }, []);

  return (
    <Section id='company'>
      <div className='company-background bg-white'>
        <div className='row'>
          <div className='col-lg-7'>
            <table className='table'>
              <tbody>
                {props.data &&
                  props.data.map((d: any, i: number) => {
                    return (
                      <tr key={i}>
                        <th scope='row'>{d.key}</th>
                        <td>
                          {d.link ? (
                            <a
                              href={d.link}
                              target='_blank'
                              style={{
                                textDecoration: 'underline'
                              }}
                            >
                              <span
                                dangerouslySetInnerHTML={{
                                  __html: d.value
                                }}
                              />
                              <i
                                className='fas fa-external-link-alt'
                                style={{
                                  marginLeft: '0.5em'
                                }}
                              />
                            </a>
                          ) : (
                            <span
                              dangerouslySetInnerHTML={{
                                __html: d.value
                              }}
                            />
                          )}
                        </td>
                      </tr>
                    );
                  })}
              </tbody>
            </table>
          </div>
          <div className='col-lg-5'>
            <div
              id='map'
              className='w-100'
              style={{
                height: 380,
                filter: 'grayscale(100%)'
              }}
            />
            <div className='text-right'>
              <a
                className='mt-3 btn btn-outline-primary rounded px-4 py-2'
                href='https://goo.gl/maps/7fin8di13W8xvZy77'
                target='_blank'
              >
                Google Maps <i className='fas fa-map-marker-alt' />
              </a>
            </div>
          </div>
        </div>
      </div>
    </Section>
  );
};

export default Company;
